import Scroll from '../../../base/scroll/scroll'
import Coupons from '../../../base/coupons/coupons'
import { mapGetters } from 'vuex'
export default {
  name: "userCoupons",
  components: {
    Scroll,
    Coupons
  },
  data() {
    return {
      pullup: true,
      pageSize: 10,
      pageNo: 1,
      messageList: [],
      pageNos:1
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  created() {
    this.getMessage()
  },
  methods: {
    readInfo(item) {
      this.$router.push({ name: 'userMessageInfo', params: { message: item } })
    },
    getMessage() {
      this.$axios.post('/api/buyer/message/getMessageListPageByBuyerId', {
        buyerUserId: this.userInfo.buyerUserId,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }).then((response) => {
        this.$vux.loading.hide()
        let data = response.data
        if (data.code !== "200") {
          this.$vux.alert.show({
            title: '',
            content: data.message,
          })
        } else {
          let myList = []
          for (let item of data.data.datas) {
            myList.push({
              time: item.gmtCreate,
              title: item.title,
              info: item.content,
              isRead: item.isRead
            })
          }
          this.pageNos = data.data.pageNos
          this.messageList = [...this.messageList, ...myList]
          this.$nextTick(() => {
            this.$refs.scrollContent.refresh()
          })
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    scrollLoad() {
      this.pageNo++
      if(this.pageNo > this.pageNos){
        this.$vux.toast.text('', 'bottom')
      }else{
        this.getMessage()
      }
    }
  }
}



